import { BehaviorSubject } from 'rxjs';
import { KycQuestion } from '../public-api';

type Translations = { [index: string]: { [index: string]: string } };
export abstract class KycQuestionsOverviewBase {
    constructor() {
        this.language = new BehaviorSubject<string>('sv');
    }

    public language: BehaviorSubject<string>;

    public languages = [
        {
            code: 'sv',
            name: 'Svenska',
        },
        {
            code: 'fi',
            name: 'Suomi',
        },
    ];

    protected setup(clientCountry: string, language?: string) {
        if (clientCountry === 'FI') {
            this.languages = [
                {
                    code: 'fi',
                    name: 'Suomi',
                },
                {
                    code: 'sv',
                    name: 'Svenska',
                },
            ];
        } else {
            this.languages = [
                {
                    code: 'sv',
                    name: 'Svenska',
                },
            ];
        }
        if (language && this.languages.find((x) => x.code === language)) {
            this.language.next(language);
        } else {
            this.language.next(this.languages[0].code);
        }
    }

    public staticTranslations: Translations = {
        explanationText: {
            sv: 'Fr att f behlla dig som kund mste vi ha uppdaterad information om dig och ditt engagemang. Drfr r det viktigt att du besvarar och uppdaterar alla frgor med aktuell information.',
            fi: 'Jotta voimme silytt sinut asiakkaanamme, meidn tytyy olla pivitetty tietoa sinusta ja sitoutumisestasi. Siksi on trke, ett vastaat ja pivitt kaikki kysymykset ajantasaisilla tiedoilla.',
        },
        explanationTextUpdate: {
            sv: 'Du behver uppdatera din KYC-information (Know your customer). Det r viktigt fr oss att ha aktuell information om dig och hur du anvnder vra tjnster, s att vi kan ge dig bsta mjliga kundupplevelse och uppfylla vra lagstadgade skyldigheter.',
            fi: 'Asiakkaan tuntemisen tietosi (KYC - Know your customer) tulisi pivitt. Meille on trke tuntea sinut ja kuinka kytt palveluitamme, jotta voimme palvella sinua parhaalla mahdollisella tavalla ja voidaksemme tytt lain meille asettamat velvollisuudet.',
        },
        language: {
            sv: 'Sprk',
            fi: 'Kieli',
        },
        back: {
            sv: 'Tillbaka',
            fi: 'Takaisin',
        },
        loanTitle: {
            sv: 'Ln',
            fi: 'Luotto',
        },
        savingsTitle: {
            sv: 'Sparkonton',
            fi: 'Ssttili',
        },
        kyc: {
            sv: 'Kundknnedom',
            fi: 'Asiakkaan tuntemus',
        },
    };

    public getRelationTypeTranslationKey(relationType: string) {
        if (relationType === 'Credit_UnsecuredLoan') {
            return 'unsecuredLoan';
        } else if (relationType === 'SavingsAccount_StandardAccount') {
            return 'savingsAccount';
        } else {
            return relationType;
        }
    }

    public getQuestionOptionFormKey(
        question: KycQuestion,
        option: { value: string; translations: { [index: string]: string } }
    ) {
        return `${question.key}_${option.value}`;
    }

    public getTranslation(translations: { [index: string]: string }) {
        if (!translations) {
            return '';
        }
        let preferredTranslation = translations[this.language.value];
        if (preferredTranslation) {
            return preferredTranslation;
        } else {
            let availableTranslations = Object.keys(translations);
            if (availableTranslations.length > 0) {
                return translations[availableTranslations[0]];
            } else {
                return '';
            }
        }
    }

    getStaticTranslation(key: string) {
        return this.getTranslation(this.staticTranslations[key]) || key;
    }

    onLanguageChanged(evt: Event) {
        this.language.next((evt as any).target.value);
    }
}
